.header {
    position: sticky;
    top: 0;
    color: #252525;
    font-size: 22px;

    background-color: rgb(180, 180, 180);

    margin: 0 auto;
    padding: 20px;

    box-sizing: border-box;
    width: 90%;

    display: flex;
    justify-content: space-between;
    justify-items: baseline;
}

@media screen and (max-width: 768px) {
    .header{
        font-size: 20px;
        width: 100%;
    }
}

@media (max-width: 480px) {
    .header {
        font-size: 17px;
        padding: 10px;
    }
}